import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';
import { Text, Localizer, withText } from 'preact-i18n';
import Markdown from 'preact-markdown';

import UserEditField from '../users/edit';

import mu from '../../lib/musher-util';

@observer
class WizardStartup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            imageIdx: 0,
        };
    }

    scrollImages = (e) => {
        const { dogs } = this.props;

        const { scrollLeft, scrollWidth, clientWidth } = e.target;
        const imageStep = clientWidth;
        const imageIdx = scrollLeft / imageStep;
        // console.log({ e, imageStep, imageIdx, scrollLeft, scrollWidth, clientWidth });

        const nearestInt = Math.round(imageIdx);
        const diff = Math.abs(nearestInt - imageIdx);
        // console.log({ nearestInt, diff });
        if (Number.isInteger(imageIdx) || diff < 0.1) {
            this.setState({
                imageIdx: nearestInt,
            });
        }
    }

    async loadNews(props = this.props) {
		const { newsStore, userStore } = props.stores;
		const { isAdmin } = userStore;
		const query = {
			tag: 'wizardStartup',
		};
		if (isAdmin) {
			query.showall = 1;
		}
		await newsStore.load({ query });
	}

	componentDidMount() {
		this.loadNews();
	}

    render() {
        const { imageIdx } = this.state;
        const { appState } = this.props.stores;
        const { language = 'en' } = appState;

        const { newsStore } = this.props.stores;
		const { newsList } = newsStore;

        const darkmode = true;

        return (
            <div class='container-fluid'>
                <div class='row'>
                    <div class='fixed-bottom' style='height: 90vh;'>
                        <div
                            class='d-flex flex-row flex-nowrap no-scrollbar h-100'
                            style='overflow: auto; scroll-snap-type: x mandatory;'
                            onScroll={this.scrollImages}
                        >
                            {newsList.map((news, idx) => {
                                const editFields = mu.replaceFieldTags(news.ingress);
                                return (<>
                                    <div class='col-12 clearfix p-0 position-relative'>
                                        <div
                                            class={`w-100 h-100 text-center`}
                                            style={`
                                                flex-wrap: wrap;
                                                overflow-y: hidden;
                                                scroll-snap-align: start;
                                            `}
                                            onTouchstart={(e) => { e.stopPropagation(); }}
                                            onTouchend={(e) => { e.stopPropagation(); }}
                                            onTouchmove={(e) => { e.stopPropagation(); }}
                                        >
                                            <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 overflow-hidden' style='height: 25%;'>
                                                {(news.images && news.images[0]) && <img src={news.images[0].s3LargeLink} class='img-fluid ' />}
                                            </div>

                                            <div class='bg-white text-dark' style='height: 75%;'>
                                                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 pt-5'>
                                                    <h3>{news.title}</h3>
                                                    <Markdown markdown={mu.replaceImages(news.ingress, news.images, language, false)} markdownOpts={mu.getMarkdownOptions()} />

                                                    <div class='text-left'>
                                                        {editFields && editFields.map(field => <>
                                                            <UserEditField stores={this.props.stores} field={field.name} type={field.type} title={field.title} help={field.help} />
                                                        </>)}
                                                    </div>
                                                    <small><Markdown markdown={mu.replaceImages(news.body, news.images, language, false)} markdownOpts={mu.getMarkdownOptions()} /></small>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </>);
                            })}
                        </div>
                        <div class='w-100 text-center position-absolute bg-white text-dark' style='bottom: 20px; right: 0px;'>
                            <small>
                                <small>
                                    {newsList.map((img, idx) => <>
                                        <i class={`${idx === imageIdx ? 'fas' : 'far'} fa-circle mr-1`} />
                                    </>)}
                                </small>
                            </small>
                        </div>

                    </div>

                </div>
            </div>
        );
    }
}

export default WizardStartup;
